.NavigationItems{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

@media(max-width:500px){
    .NavigationItems{
        flex-direction: column;
        width: fit-content;
        height: 15%;
        align-items: center;
        padding: 20%;

    }
}