.SideDrawer{
    position: fixed;
    width: 35%;
    height: 100%;
    z-index: 150;
    left: 0;
    top: 0;
    background-color: white;
    color: black;
    box-sizing: border-box;
    transition: transform 0.3s ease-out;
}

.SideDrawerLightMode{
    background-color: #00ccff;
}

.SideDrawerDarkMode{
    background-color: #00001a;
}

.Open{
    transform: translateX(0);
}
.Close{
    transform: translateX(-100%);
}




@media(min-width:500px){
    .SideDrawer{
        display: none;
        
    }
}